:root {
  --gt-col: repeat(3, 1fr);
}

.main {
  /* background-color: #FFFFE0; */
  background-color: #f5f5f5;
  display: flex;
  flex-direction: row;
}

.content {
  padding: 1.5rem 2rem;
  overflow: auto;
  height: 200vh;
  /* scroll-behavior: auto; */
}
.content,
.content-section {
  /* background-color: green; */
  width: 100%;
}

.content-header {
  padding-bottom: 1.5rem;
}

.content-section-grid {
  width: 100%;
  height: max-content;
  display: grid;
  gap: 1.2rem;
  grid-template-columns: var(--gt-col);
  grid-template-rows: auto auto;
}

.content-section-grid > div {
  background-color: #fff;
  /* border: black 1px solid; */
  border-radius: 0.5rem;

  border: 1px rgba(0, 0, 0, 0.1) solid;

  box-shadow: 0 0 0.15rem rgba(0, 0, 0, 0.1);
  /* box-shadow: 0 0 0.15rem rgba(0, 0, 0, 0.1),
    0.1rem 0.1rem 0.15rem rgba(0, 0, 0, 0.1); */
}

@media only screen and (max-width: 875px) {
  :root {
    --gt-col: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 645px) {
  :root {
    --gt-col: 1fr;
  }
}

/* REPO ITEMS */
.content-section-repo-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding: 1rem;
}

.content-section-repo-item > p {
  --max-lines: 4;
  margin-top: 0.5rem;
  margin-bottom: 0.75rem;
  text-align: center;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: var(--max-lines);
}

.content-section-repo-icons {
  /* background-color: green; */
  max-width: 100%;
  min-width: 60%;
  width: 80%;
  display: flex;
  justify-content: space-between;
}

.content-section-repo-icons > div {
  display: flex;
  align-items: center;
}

.content-section-repo-items a {
  color: green;
}

.repo-icons {
  /* background-color: #FF6766; */
  min-width: 60%;
  justify-content: space-evenly;
}

.repo-stars > p, .repo-stars > ion-icon {
  color: #FF9900;
}

.repo-forks > p, .repo-forks > ion-icon {
  color: #FF6766;
}