@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:wght@100;300;400;500;700;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;

  /* font-size: 16px; */
}

:root {
  --col-accent: #11101d;
  --col-accent-sec: #1d1b31;

  /* DIMENSIONS */
  --nav-border: 12px;
  --sb-icons-ht: 50px;
  --sb-navbar-wd: 80px;
}

html,
body {
  margin: 0;
  height: 100%;
}

body {
  position: relative;
  min-height: 100vh;
  width: 100%;
  overflow-y: hidden;
}

/* outermost container */
.sb {
  top: 0;
  left: 0;
  background-color: var(--col-accent);
  height: 100vh;
  width: 80px;
  padding: 1.2rem 1rem;
  transition: all 0.5s ease;
  background-color: var(--col-accent);

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

/* when sidebar is open*/
.sb-active {
  width: 240px;
}

#menu-btn {
  /* transform: rotate(180deg); */
  transition: all 0.5 ease;
}

.sb-active #menu-btn {
  transform: rotate(180deg);
  transition: all 0.5 ease;
}

.sb .sb-header .sb-header-logo {
  color: #fff;
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
}

.sb .sb-header .sb-header-logo .sb-header-title {
  display: none;
}
.sb-active .sb-header .sb-header-logo .sb-header-title {
  display: block;
}

.sb-header-logo .sidevar-header-box-icon,
.sb-active .sb-header-logo .sidevar-header-box-icon {
  padding: auto;
  font-size: 28px;
  height: var(--sb-icons-ht);
  width: var(--sb-icons-ht);
}

.sb-header-logo .sb-header-title h2 {
  font-size: 20px;
  font-weight: 400;
}

.sb-header-menu-button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  height: var(--sb-icons-ht);
  width: var(--sb-icons-ht);
  text-align: center;
  line-height: var(--sb-icons-ht);
  cursor: pointer;
}

/* menu toggle button */
.sb #menu-btn {
  font-size: 1.5rem;
}

.sb .sb-active .sb-header-title {
  display: none;
  left: 90%;
}

/* MENU ITEMS */
.sb ul {
  /* margin-top: 2rem; */
  height: 80vh;
  /* position: fixed;
    top: 0;
    left: 0;
    right: 0; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.sb ul li {
  position: relative;
  height: var(--sb-icons-ht);
  width: 100%;
  list-style: none;
  line-height: var(--sb-icons-ht);
  border-radius: var(--nav-border);
}

/* tooltip */
.sb ul li .sb-menu-tooltip {
  position: absolute;
  height: 35px;
  width: 122px;
  left: 122px;
  top: 0;
  transform: translate(-50%, -50%);
  border-radius: 6px;
  background: #fff;
  line-height: 35px;
  text-align: center;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  transition: 0s;
  opacity: 0;
  pointer-events: none;
}

.sb-passive ul li:hover .sb-menu-tooltip {
  transition: all 0.5s ease;
  top: 50%;
  opacity: 1;
}

.sb ul li a {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: all 0.4s ease;
  border-radius: var(--nav-border);
  flex-basis: 0;
}
.sb ul li a > div {
  flex-shrink: 0;
}
.sb ul li a > span {
  display: none;
  flex-shrink: 1;
  /* transition: all 0.5s ease; */
  /* margin: 0 12spx; */
}

.sb-active ul li a > span {
  display: block;
  margin-left: 0.5rem;
  /* transition: all 0.5s ease; */
  /* margin: 0 12spx; */
}

.sb ul li .sb-search-box {
  height: 100%;
  width: 100%;

  display: flex;
  flex-wrap: nowrap;

  border-radius: var(--nav-border);
  outline: none;
  border: none;
  background-color: var(--col-accent-sec);
  font-size: 18px;
  color: #fff;
}

.sb ul li .sb-search-box input {
  background-color: red;
  flex-shrink: 1;
}

.sb-search-box-icon {
  height: 100%;
  width: 100%;
  font-size: 1.5rem;
  background-color: green;
}

.sb ul li a:hover {
  color: var(--col-accent);
  background-color: #fff;
}
.sb ul li a:hover .sb-menu-box-icon {
  color: var(--col-accent);
}

.sb ul li a div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--sb-icons-ht);
  /* min-width: 20px; */
  height: var(--sb-icons-ht);
  padding: 0;
  text-align: center;
  /* font-size: 40px; */
}

.sb-menu-box-icon {
  height: 100%;
  width: 100%;
  font-size: 1.5rem;
  /* margin: 0.2rem; */
  /* transform: translateY(-50%); */
}

@media only screen and (max-width: 600px) {
  body {
    overflow-x: hidden;
    overflow-y: auto;
  }

  .main {
    display: flex;
    flex-direction: column;
  }

  .sb {
    display: flex;
    flex-direction: row;
    height: var(--sb-icons-ht);
    width: 100vw;
    padding: 0;
    transition: all 0.5 ease;
  }

  .sb-active {
    height: max-content;
    justify-content: space-evenly;
    transition: all 0.5 ease;
  }

  .sb-passive > .sb-menu {
    visibility: hidden;
  }
  
  .sb-menu {
    align-self: flex-start;
    flex: 0 0 200px;
  }

  .sb-header-menu-button {
    transform: rotate(90deg);
  }
}
